<template>
  <section class="chart-container">
    <el-row>
      <el-col :span="24">
        <h4>任务总数： 100</h4>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="12">
        <div id="chartColumn" style="width:100%; height:400px;"></div>
      </el-col>
      <el-col :span="12">
        <div id="chartPie" style="width:100%; height:400px;"></div>
      </el-col>
    </el-row>
  </section>
</template>

<script>
import echarts from "echarts";

export default {
  name: "order",
  data() {
    return {
      chartColumn: null,
      chartPie: null
    };
  },

  methods: {
    drawColumnChart() {
      this.chartColumn = echarts.init(document.getElementById("chartColumn"));

      this.chartColumn = echarts.init(document.getElementById("chartColumn"));
      this.chartColumn.setOption({
        title: {
          text: "任务结果",
          subtext: "备注说明",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          left: "left",
          data: ["执行成功任务数量", "执行失败任务数量"]
        },
        series: [
          {
            name: "按运行状态分类",
            type: "pie",
            radius: "55%",
            center: ["50%", "60%"],
            data: [
              { value: 135, name: "执行成功任务数量" },
              { value: 1548, name: "执行失败任务数量" }
            ],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ],
        itemStyle: {
          emphasis: {
            shadowBlur: 10,
            shadowOffsetX: 0,
            shadowColor: "rgba(0, 0, 0, 0.5)"
          },
          normal: {
            color: function(params) {
              //自定义颜色
              var colorList = ["#1A8FED", "#FE8463", "#FE8463"];
              return colorList[params.dataIndex];
            }
          }
        }
      });
    },
    drawPieChart() {
      this.chartPie = echarts.init(document.getElementById("chartPie"));
      this.chartPie.setOption({
        title: {
          text: "任务状态",
          subtext: "备注说明",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          left: "left",
          data: ["运行的任务数量", "中止的任务数量"]
        },
        series: [
          {
            name: "按运行状态分类",
            type: "pie",
            radius: "55%",
            center: ["50%", "60%"],
            data: [
              { value: 135, name: "运行的任务数量" },
              { value: 1548, name: "中止的任务数量" }
            ],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ],
        itemStyle: {
          emphasis: {
            shadowBlur: 10,
            shadowOffsetX: 0,
            shadowColor: "rgba(0, 0, 0, 0.5)"
          },
          normal: {
            color: function(params) {
              //自定义颜色
              var colorList = ["#1A8FED", "#FE8463", "#FE8463"];
              return colorList[params.dataIndex];
            }
          }
        }
      });
    },
    drawCharts() {
      this.drawColumnChart();
      this.drawPieChart();
    }
  },

  mounted: function() {
    this.drawCharts();
  },
  updated: function() {
    this.drawCharts();
  }
};
</script>

<style scoped>
.chart-container {
  width: 100%;
  float: left;
}
/*.chart div {
        height: 400px;
        float: left;
    }*/

.el-col {
  padding: 30px 20px;
}
</style>
